/* main-wrapper
//card = wrapper
//img-containet = img-wrapper */

.main-wrapper {
  contain: layout inline-size style;
  display: flex;
  justify-content: space-evenly;
}

.card {
  display: flex;
  flex-direction: column;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 100px;
  margin: 2em;
  background-color: rgb(231, 209, 41);  
  border-radius: 2px;
/*   box-shadow: 0 3px 6px rgb(231, 209, 41), 0 3px 6px #999; */
  text-align: left;
  border: 1.5px solid rgb(3, 3, 3);
  transition: all 0.3s linear;
  box-sizing: border-box;
  -webkit-transition: box-shadow 200ms ease,-webkit-transform 200ms ease;
  transition: box-shadow 200ms ease,transform 200ms ease;
}

.card-creative {
  align-items: center;
  justify-content: center;
  width: 800px;
  height: 800px;
}

.card-creative__p5Art {
  width: 80%;
  height: 80%;
}
  
.img-container {
  width: 100%;
  height: auto;
  overflow: hidden;
  text-align: center;
  background: rgb(231, 209, 41); 
  transition-timing-function: ease;
  transition-delay: 0s;
  transition-duration: 1s;
}

.card > .img-container > img {
  position: relative;
  width: 100%;
  
}

.card > .content {
  padding-top: .75rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;

}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
/*   margin-top: 18px; */
}

/*  .main-wrapper{
  font-size: 0mm;
} */

.button-creative {
  background: none !important;
  border: none !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: red;
}

  @container (max-width: 800px) {
    .info {
      display: flex;
    }

    .main-wrapper {
      display: flex;
      font-size: 1em;
    }
  
    h2 {
      margin-right: 25px;
    }
  
    .notes {
      width: 75%;
      margin: 2rem auto;
    }
  }

  @media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3) { 

    .main-wrapper {
      display: flex;
      font-size: em;
    }

   /*  .card {
      height: 800px;
    } */

    .card-creative {
      width: 315px;
      height: 800px;
    }

}

/* Portrait */
@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3)
  and (orientation: portrait) { 

    * {
      box-sizing: none;
    }
  
  .info {
    display: flex;
  }

  .main-wrapper {
    display: flex;
    object-fit: cover;
  
  }

  .card {
    display: flex;
  }

  .card-creative {
    width: 315px;
    height: 800px;
  }

  .img-container{
    size: 1vh;
    object-fit: cover;
    overflow: none;
  }

  h2, h6 {
    margin: 1rem auto;
  }    
}

/* Landscape */
@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3)
  and (orientation: landscape) { 
    

    * {
      box-sizing: none;
    }
  
  .info {
    display: flex;
  }

  .main-wrapper {
    display: flex;
    object-fit: cover;
  
  }

  .card {
    display: flex;
  }

  .img-container{
    size: 1vh;
    object-fit: cover;
    overflow: none;
  }

  h2, h6 {
    margin: 1rem auto;
  }    

}